footer {
  background: #fce6c6;
}
.top-footer {
  padding: 70px 0 50px;
  color: #515151;
  font-family: "Poppins-Medium";
  @media (max-width: 767.9px) {
    padding: 20px 0;
  }
  .logo {
    width: 78px;

    @media (max-width: 767.9px) {
      margin-bottom: 8px !important;
    }
  }

  p {
    width: 290px;
    line-height: 2em;
    margin-bottom: 0;
  }
  .social-links {
    margin-top: 1rem;
    span {
      margin-right: 0.5rem;
      transform: scale(0.9);
    }
  }
  li {
    line-height: 3em;
  }
  .input-group {
    @include border-radius(0);
    background: #fff;
    box-shadow: 0px 20px 26px rgba(0, 0, 0, 0.08);
    .form-control {
      @include border-radius(0);
      background: transparent;
      border: 0;
      &::placeholder {
        font-size: 0.8em;
        color: rgba(122, 122, 122, 0.5);
      }
    }
    .btn {
      @include border-radius(0!important);
      background: $main-color;
      color: #fff;
      width: 150px;
      border: 0;
    }
  }
  h6 {
    color: #1f1f1f;
    margin-top: 3rem;
    @media (max-width: 767.9px) {
      margin-bottom: 8px !important;
    }
  }
  .f-col {
    @media (max-width: 991.9px) {
      flex: 0 0 50%;
      margin-bottom: 15px;
    }

    &:first-child {
      @media (max-width: 767.9px) {
        flex: 0 0 100%;
      }
    }
    &:last-child {
      @media (min-width: 768px) and (max-width: 991.98px) {
        width: 315px;
      }
      .btn {
        @media (max-width: 991.98px) {
          width: 130px;
        }
      }
    }
  }
}
.bottom-footer {
  padding: 0.8rem;
  .copywrite {
    font-size: 0.8rem;
    color: #8e8d8e;
  }
}
footer {
  .d-flex {
    @media (max-width: 575.9px) {
      flex-direction: column;
    }
  }
}
