header {
  padding: 1rem 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  .logo {
    width: 78px;
  }
  li {
    display: inline-flex;
    color: $black-900;
    font-family: "Poppins-Bold";
    margin: 0 1rem;
    position: relative;
    padding-bottom: 5px;
    @media (min-width: 991.9px) and (max-width: 1200px) {
      font-size: 0.9em;
    }
    @media (max-width: 991.9px) {
      display: flex;
      padding: 0.5rem 0;
    }
    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 0;
      height: 2px;
      background: transparent;
    }
    &.active,
    &:hover {
      color: $main-color;
      &:after {
        width: 100%;
        background: $main-color;
      }
    }
  }
  .btn {
    @include border-radius(5px);
    padding: 0.5rem 2rem;
    min-width: 125px;
    @media (max-width: 1199.9px) {
      padding: 0.4rem 1rem;
    }
  }
  .menu {
    @media (max-width: 991px) {
      height: 100vh;
      background: #fff;
      width: 280px;
      position: fixed;
      top: 0;
      right: -300px;
      padding: 2rem 1rem;
      transition: right 1s;
      z-index: 10;
      display: flex;
      align-items: center;
    }
    .logo {
      position: absolute;
      left: 1rem;
      top: 30px;
    }
    &.active {
      right: 0;
    }
  }
}

.ico-sidemenu {
  svg {
    path {
      fill: $main-color;
    }
  }
}

.overlay {
  background: #000;
  opacity: 0.8;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  z-index: 9;
  display: none;
  &.active {
    display: block;
  }
}
.icon-close {
  position: absolute;
  right: 15px;
  top: 15px;
  cursor: pointer;
  svg {
    width: 15px;
    path {
      fill: $main-color;
    }
  }
}
