.home__banner {
  // padding-bottom: 200px;
  position: relative;
  background: url(../images/shape-left.svg) no-repeat left bottom;
  position: relative;
  margin-bottom: 8rem;
  padding-top: 110px;
  @media (max-width: 991.9px) {
    background: none;
  }
  &:after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 40%;
    height: 100%;
    background: url(../images/shape1-cover.png) no-repeat right bottom;
    @media (max-width: 1199.9px) {
      background-size: 100%;
    }
    @media (max-width: 767.9px) {
      background: none;
      display: none;
    }
  }
  .img-banner {
    position: relative;
    z-index: 100;

    @media (max-width: 767.9px) {
      background: url(../images/shape1-cover.png) no-repeat right bottom;
      background-size: 100%;
      margin-bottom: 20px;
    }

    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      background: url(../images/shapes-cover.png) no-repeat bottom 50px right;

      @media (max-width: 767.9px) {
        background: url(../images/shapes-cover.png) no-repeat center;
      }
    }
    &:hover {
      &:after {
        animation: slide1 1s ease-in-out infinite;
        left: 3px;
      }
    }
  }
  .desc-slide {
    p {
      color: $grey-txt;
      font-size: 0.8rem;
      width: 70%;
      margin: 20px 0;
      @media (max-width: 767.9px) {
        width: 100%;
      }
    }
  }
  .actions {
    .btn {
      width: 165px;
      height: 50px;
      border-radius: 5px;
      font-size: 0.9rem;
      @media (max-width: 991.9px) {
        // width: 135px;
      }
      &.btn-register {
        border-width: 3px;
        margin-left: 12px;
      }
    }
  }
}

.social-icons {
  display: flex;
  li {
    margin: 0 0.5rem;
    opacity: 0.87;
    a {
      display: inline-flex;
    }
    i {
      width: 30px;
      height: 30px;
      @include border-radius(50%);
      border: 1px solid $secondary-color;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      svg {
        transform: scale(0.8);
      }
    }
  }
}

.about {
  .points {
    position: relative;
    &:before {
      content: "";
      width: 4px;
      height: 79%;
      border-radius: 2px;
      background: #b5b5b5;
      position: absolute;
      top: 2px;
      left: 7px;
    }
  }
  .point {
    color: $grey-txt;
    margin-bottom: 30px;
    width: 80%;
    padding-left: 40px;
    position: relative;

    svg {
      position: absolute;
      top: 2px;
      left: 0;
    }
    h6 {
      margin-bottom: 20px;
      font-family: Poppins-Medium;
    }
    p {
      line-height: 1.5em;
    }
    &:first-child {
      h6 {
        color: $main-color;
      }
    }
  }
}
.partners {
  .title {
    &.w-line {
      &:after {
        width: 70px;
        left: 0;
        bottom: 10px;
      }
    }
  }
  .slick-track {
    display: flex;
    align-items: center;
  }

  .logo-partner {
    img {
      margin: 0 auto;
      transform: scale(0.8);
    }
  }
}
.getapp {
  background: $link-color;
  color: #fff;
  padding: 50px 0 0;
  .actions {
    .btn {
      width: 180px;
      padding: 5px;
      @media (max-width: 767.9px) {
        width: 140px;
      }
    }
  }

  .ring {
    margin-top: -50px;

    @media (max-width: 991.9px) {
      transform: scale(0.8, 0.8);
    }
    @media (max-width: 767.9px) {
      transform: scale(0.7, 0.7);
    }
  }
  .coccoc-alo-ph-img-circle {
    top: -95px;
  }
}
.m__title {
  margin-bottom: 80px;
}
.title {
  margin-bottom: 20px;
  &.custom.w-line {
    &:after {
      left: 0;
      width: 80px;
      bottom: 5px;
    }
  }
}
.order {
  .ico-order {
    margin-bottom: 20px;
    height: 60px;
  }
  h5 {
    margin-bottom: 15px;
  }
  p {
    color: $grey-txt;
  }
  .num {
    margin-top: 60px;
    @media (max-width: 767.9px) {
      margin-top: 20px;
      margin-bottom: 50px;
    }
  }
  .items-order {
    margin: 40px 0;
    padding-bottom: 100px;
    background: url(../images/curve.svg) no-repeat top center;
    @media (max-width: 991.9px) {
      background: none;
    }
  }
}
.items-customers {
  .job {
    font-size: 0.8rem;
    margin-bottom: 20px;
  }
  p {
    width: 70%;
    letter-spacing: 0.04em;

    color: #1e1e1e;
  }
  .slider-actions {
    i {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background: linear-gradient(rgba(243, 146, 0, 0.4) 0%, rgba(255, 125, 24, 0.4) 100%);
      &:hover,
      .active {
        background: linear-gradient(#f39200 0%, #ff7d18 100%);
        svg {
          path {
            fill: #fff;
          }
        }
      }
    }
    .lSNext {
      margin-left: 20px;
      i {
        transform: rotate(180deg);
      }
    }
  }
  .cover-cust {
    position: relative;
    .c {
      position: absolute;
      &.c1 {
        left: 14%;
        top: 17%;
      }
      &.c2 {
        left: 14%;
        bottom: 17%;
      }
      &.c3 {
        top: 38%;
        left: 39%;
      }
      &.c4 {
        right: 20%;
        top: 17%;
      }
      &.c5 {
        right: 20%;
        bottom: 17%;
      }
    }
  }
}
