body {
  font-size: 0.9rem;

  overflow-x: hidden;
  font-family: "Poppins-Regular";
  color: rgb(0, 0, 0);

  background: #fff;

  @media (max-width: 767.9px) {
    font-size: 0.8rem;
  }
}
h3 {
  line-height: 1.5;
}
h5 {
  @media (max-width: 767.9px) {
    font-size: 1rem;
  }
}

.w-meduim {
  font-family: Poppins-Medium;
}

.w-bold {
  font-family: Poppins-Bold;
}
.link-color {
  color: $link-color;
}
.txt-md {
  font-size: 0.9rem;
}
.txt-sm {
  font-size: 0.8rem;
}
a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.pt-header {
  padding-top: 160px;
}
img {
  max-width: 100%;
}

.main-color {
  color: $main-color;
}
.sec-color {
  color: $secondary-color;
}
.c-grey {
  color: #5f5982;
}
.grey-txt {
  color: #8b909e;
}
.bg-main {
  background: $main100-color;
}
.rounded-15 {
  @include border-radius(15px);
}

.mb__section {
  margin: 6rem 0;
  @media (max-width: 991.9px) {
    margin: 3rem 0;
  }
}
h6 {
  @media (max-width: 575.9px) {
    font-size: 1em;
  }
}
.mb-6 {
  margin-bottom: 4rem;
}
@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
p {
  line-height: 2em;
}
.search__block__mobile {
  margin-bottom: 1rem;
  .btn-search {
    padding: 1rem;
    margin: 0 auto;
    width: 90%;
    border: 1px solid $secondary-color;
    @include border-radius(15px);
    cursor: pointer;
  }
}
.searchMobile {
  display: none;
  position: fixed;
  z-index: 100;
  top: 0;
  min-height: 100vh;
  width: 100%;
  background: #fff;
  padding: 0;
  &.open {
    display: block;
  }
  .close-search {
    position: absolute;
    top: calc(1rem - 3px);
    left: 1rem;
    width: 20px;
    height: 15px;
    cursor: pointer;
    svg {
      transform: scale(0.7);
      path {
        fill: #fff;
      }
    }
  }
  .title {
    background: $main-color;
    padding: 1rem;
    color: #fff;
  }
  .list-filter {
    li {
      padding: 2rem 1rem;
      border-bottom: 1px solid #d9d9d9;
    }
  }
}
.animate {
  -moz-opacity: 0;
  -khtml-opacity: 0;
  -webkit-opacity: 0;
  opacity: 0;
  filter: alpha(opacity=0 * 100);
  @media (max-width: 575.9px) {
    -moz-opacity: 1;
    -khtml-opacity: 1;
    -webkit-opacity: 1;
    opacity: 1;
    filter: alpha(opacity=1 * 100);
  }
}

.animated {
  -moz-opacity: 1;
  -khtml-opacity: 1;
  -webkit-opacity: 1;
  opacity: 1;
  filter: alpha(opacity=1 * 100);
}
@keyframes slide1 {
  0%,
  100% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(10px, 0);
  }
}
