.download__banner {
  background: linear-gradient(rgba(243, 146, 0, 0.07) 0%, rgba(255, 255, 255, 0.5) 100%);

  p {
    margin: 30px 0;
  }
  .actions {
    .btn {
      padding: 0;
      margin-right: 0.8rem;
      width: 155px;
      @media (max-width: 575.9px) {
        width: 120px;
      }
    }
  }
  .cover-banner {
    position: relative;
    @media (max-width: 767.9px) {
      width: 80%;
      margin: 0 auto 60px;
    }
    .layer {
      position: absolute;
      &.layer1 {
        top: 20px;
        left: 0;
        @media (max-width: 1199.9px) {
          left: -40px;
        }
      }
      &.layer2 {
        bottom: 120px;
        right: 0;
        @media (max-width: 1199.9px) {
          right: -40px;
        }
      }
      &.layer3 {
        bottom: -45px;
        left: 15px;
      }
    }
  }
}

.register {
  background: $link-color;
  padding: 100px 0 200px;
  @media (max-width: 767.9px) {
    padding: 50px 0;
  }
  p {
    color: #ebebeb;
    line-height: 2em;
  }
  .form-wrapper {
    padding: 30px;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
    position: absolute;
    width: 100%;
    right: 0;
    z-index: 100;
    @media (max-width: 767.9px) {
      position: relative;
    }
    &:before {
      content: "";
      position: absolute;
      top: -30px;
      left: -65px;
      width: 300px;
      height: 255px;
      background: url(../images/bgform.svg) no-repeat;
      background-size: 100%;
      z-index: -1;
    }
  }
}
.grey-form {
  .form-control {
    border-radius: 12px;
    background-color: #fff;
    border: 1px solid #a7a6a6;
    box-shadow: none;
  }
  .btn {
    border-radius: 12px;
    height: 50px;
  }
}
.express {
  margin: 150px 0;
  @media (max-width: 767.9px) {
    margin: 50px 0;
  }
  .express-txt {
    margin-bottom: 100px;
    @media (max-width: 767.9px) {
      margin-bottom: 50px;
    }
  }
  .ring {
    @media (max-width: 991.9px) {
      transform: scale(0.8);
    }
    @media (max-width: 767.9px) {
      transform: scale(0.8) translateX(-15%);
    }
  }
}

/***********************Ring****************/
.coccoc-alo-ph-circle {
  width: 480px;
  height: 480px;
  top: -20px;
  left: -20px;
  position: absolute;
  background-color: transparent;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  border: 2px solid rgba(30, 30, 30, 0.4);
  opacity: 0.1;
  -webkit-animation: coccoc-alo-circle-anim 1.2s infinite ease-in-out;
  -moz-animation: coccoc-alo-circle-anim 1.2s infinite ease-in-out;
  -ms-animation: coccoc-alo-circle-anim 1.2s infinite ease-in-out;
  -o-animation: coccoc-alo-circle-anim 1.2s infinite ease-in-out;
  animation: coccoc-alo-circle-anim 1.2s infinite ease-in-out;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.coccoc-alo-phone {
  background-color: transparent;
  width: 440px;
  height: 440px;
  cursor: pointer;
  z-index: 200000 !important;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0);
  -webkit-transition: visibility 0.5s;
  -moz-transition: visibility 0.5s;
  -o-transition: visibility 0.5s;
  transition: visibility 0.5s;
}

.coccoc-alo-phone.coccoc-alo-green .coccoc-alo-ph-circle-fill {
  background: #f39200;
}

.coccoc-alo-ph-circle-fill {
  width: 90%;
  height: 90%;
  top: 5%;
  left: 5%;
  position: absolute;
  background: #ffa51e;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  -webkit-animation: coccoc-alo-circle-fill-anim 2.3s infinite ease-in-out;
  -moz-animation: coccoc-alo-circle-fill-anim 2.3s infinite ease-in-out;
  -ms-animation: coccoc-alo-circle-fill-anim 2.3s infinite ease-in-out;
  -o-animation: coccoc-alo-circle-fill-anim 2.3s infinite ease-in-out;
  animation: coccoc-alo-circle-fill-anim 2.3s infinite ease-in-out;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  &.fill2 {
    background: #ffa51e !important;
    opacity: 0.58;
    width: 80%;
    height: 80%;
    top: 10%;
    left: 10%;
  }
  &.fill3 {
    background: #f9f2e7 !important;
    opacity: 0.58;
    width: 70%;
    height: 70%;
    top: 15%;
    left: 15%;
  }
}

.coccoc-alo-phone.coccoc-alo-green .coccoc-alo-ph-circle {
  background: #f39200;
}

.coccoc-alo-ph-img-circle {
  text-align: center;
  z-index: 10000;
  position: absolute;
  width: 100%;
  height: 100%;
  top: -50px;
}

@keyframes coccoc-alo-circle-anim {
  0% {
    transform: rotate(0) scale(0.5) skew(1deg);
    opacity: 0.1;
  }
  30% {
    transform: rotate(0) scale(0.7) skew(1deg);
    opacity: 0.5;
  }
  100% {
    transform: rotate(0) scale(1) skew(1deg);
    opacity: 0.1;
  }
}

@keyframes coccoc-alo-circle-img-anim {
  0% {
    transform: rotate(0) scale(1) skew(1deg);
  }
  10% {
    transform: rotate(-25deg) scale(1) skew(1deg);
  }
  20% {
    transform: rotate(25deg) scale(1) skew(1deg);
  }
  30% {
    transform: rotate(-25deg) scale(1) skew(1deg);
  }
  40% {
    transform: rotate(25deg) scale(1) skew(1deg);
  }
  50% {
    transform: rotate(0) scale(1) skew(1deg);
  }
  100% {
    transform: rotate(0) scale(1) skew(1deg);
  }
}

/******************** End Ring ***************************/
.step {
  cursor: pointer;
  margin-bottom: 40px;
  .step-num {
    width: 70px;
    height: 70px;
    border-radius: 14px;
    background: #fce6c6;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    span {
      font-weight: bold;
      font-size: 1rem;
      color: $main-color;
    }
  }
  h6 {
    margin-bottom: 15px;
  }
  p {
    font-size: 0.8rem;
  }
  &:hover {
    .step-num {
      background: $main-color;
      span {
        color: #fff;
      }
    }
  }
}
.social-links {
  a {
    margin-right: 8px;
  }
}
