.login-form {
  padding-bottom: 50px;
}
.box {
  .wrapper__box {
    width: 400px;
    margin: 0 auto;
    @media (max-width: 575.9px) {
      max-width: 98%;
    }
    &.box-lg {
      width: 600px;
      @media (max-width: 991.9px) {
        max-width: 100%;
      }
    }
  }

  .title__box {
    margin-bottom: 3rem;
    h5 {
      margin-bottom: 0.8rem;
    }
    p {
      color: #787f87;
      text-align: center;
    }
  }
}
.form-check {
  margin-right: 30px;
  .form-check-label {
    color: $black-900;
    line-height: 1.7rem;
    padding: 0 0.5em;
    font-size: 1rem;
  }
  .form-check-input {
    border: 1px solid $main-color;
    width: 20px;
    height: 20px;
    box-shadow: none;
    &:checked {
      background-image: url(../images/checked.svg);
      border: 0;
    }
  }
}
.input-pass {
  position: relative;
  a {
    position: absolute;
    top: 13px;
    right: 10px;
  }
}

.cs-hr {
  margin: 2rem 0;
  position: relative;
  h5 {
    background: #fff;
    padding: 0 1.2rem;
    display: inline-block;
    z-index: 2;
    position: relative;
  }
  &:before {
    content: "";
    background: #cac6d4;
    width: 100%;
    height: 1px;
    position: absolute;
    top: 12px;
    left: 0;
  }
}
.form-control {
  border: 0;

  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  @include border-radius(5px);
  padding: 0.775rem 0.75rem;
  &::placeholder {
    font-size: 0.8em;
    color: rgba(122, 122, 122, 0.5);
  }
  &:focus {
    border-color: $main-color;
  }
}
select:invalid {
  font-size: 0.9em;
  color: rgba(122, 122, 122, 0.5);
}

label {
  color: #302d3a;
}
.btn {
  &.btn-solid {
    background: $secondary-color;
    color: #fff;
    &.btn-grey {
      background: rgba(187, 182, 182, 0.38);
      color: #707070;
    }
  }
  &.btn-solid-primary {
    background: $main-color;
    color: #fff;
  }
  &.btn-outline {
    border: 1px solid $secondary-color;
    color: $secondary-color;
    &.btn-main-color {
      border: 1px solid $main-color;
      color: $main-color;
    }
  }
  &.btn-shadow {
    box-shadow: 0px 12px 22px rgba(238, 53, 134, 0.3);
  }
  &.btn-md {
    width: 190px;
    height: 56px;

    @media (max-width: 991.9px) {
      width: 175px;
      height: 45px;
      font-size: 0.9em;
    }
  }
  &.btn-rounded {
    @include border-radius(35px);
  }
  &.btn-form {
    background: $link-color;
    color: #fff;
    height: 50px;
    @include border-radius(5px);
  }
  &.btn-social {
    @include border-radius(4px);
    color: #fff;
    width: 200px;
    height: 45px;
    font-family: Poppins-Medium;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    &.btn-fb {
      background: #3c5a96;
      box-shadow: 0px 3px 6px rgba(64, 146, 251, 0.2);
      margin-right: 40px;
      @media (max-width: 575.9px) {
        margin-right: 20px;
      }
    }
  }
  &.btn-google {
    img {
      width: 25px;
      margin-right: 8px;
    }
  }
}

.gender-options {
  display: flex;
  align-items: center;
  .option {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    i {
      width: 50px;
      height: 50px;
      @include border-radius(50%);
      background: #f5f8f9;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      margin-right: 20px;
      @media (max-width: 991.9px) {
        width: 40px;
        height: 40px;
        margin-right: 10px;
      }
      svg {
        @media (max-width: 991.9px) {
          transform: scale(0.8);
        }
      }
    }
    &.active {
      i {
        background: $secondary-color;
        svg {
          path[fill="#b1bfcd"] {
            fill: #fff;
          }
        }
      }
    }
  }
}
/* ===================================== SignUp =====================================*/
.signup-form {
  padding: 3rem 0;
  .cs-hr {
    &:before {
      content: "";
      background: rgba(202, 214, 227, 0.6);
      width: 240px;
      height: 2px;
      position: absolute;
      top: 12px;
      left: calc(50% - 120px);
    }
  }
}

/* ===================================== Information =====================================*/
.nav-signup {
  text-align: center;
  .nav-tabs {
    border-radius: 12px;
    background: rgba(243, 146, 0, 0.15);
    border: 0;
    display: inline-flex;
    margin: 0 auto 40px;
    .nav-link {
      color: #afafaf;
      font-family: Poppins-Medium;
      font-size: 0.8rem;
      border: 0;
    }
    .nav-link.active,
    .nav-item.show .nav-link {
      background: $main-color;
      color: #fff;
      border-radius: 12px;
      box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.16);
    }
  }
}
.dropzone {
  min-height: 100px;
  border-radius: 5px;
  background: #fdf2e2;
  border: 1px dashed #f39200;
}
.dropzone.dz-clickable .dz-message,
.dropzone.dz-clickable .dz-message * {
  text-align: left;
  color: rgba(122, 122, 122, 0.5);
  font-size: 0.9rem;
}
.register_file_content {
  text-align: justify;
  color: #dc3545;
  font-size: 0.7rem;
  line-height: 1.5em;
}
