.title {
    margin: 0 0 40px;
    position: relative;
    z-index: 1;
    &.w-line {
        &:after {
            content: "";
            position: absolute;
            left: 10px;
            bottom: 0px;
            width: 38px;
            height: 18px;
            background: rgba(243, 146, 0, 0.51);
            z-index: -1;
            @media (max-width: 767.9px) {
                left: 20px;
                top: 10px;
                bottom: auto;
            }
        }
    }
}
.list-categories {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    li {
        @include border-radius(8px);
        background: #fff;
        border: 1px solid #ededed;
        padding: 20px;
        width: 24%;
        flex: 0 0 24%;
        min-height: 220px;
        align-items: center;
        margin-bottom: 20px;
        transition: all 0.5s ease-out;
        @media (max-width: 991.9px) {
            width: 32%;
            flex: 0 0 32%;
        }
        @media (max-width: 767.9px) {
            width: 48%;
            flex: 0 0 48%;
        }
        @media (max-width: 575.9px) {
            width: 300px;
            flex: 300px;
        }
        a {
            display: flex;
            width: 100%;
            height: 100%;
            flex-direction: column;
            justify-content: center;
        }
        &.more-categ {
            background: $main-color url(../images/bgmore.svg) no-repeat top -29px center !important;
            background-size: cover !important;
            h4 {
                color: #fff !important;
            }
            a {
                text-align: center;
            }
            svg {
                animation: slide1 1s ease-in-out infinite;
                margin-left: 9px;
            }
        }
        h6 {
            font-weight: bold;
            white-space: pre-line;
            color: #183b56;
        }
        .icon-category {
            width: 65px;
            height: 65px;
            background: rgba(243, 146, 0, 0.1);
            @include border-radius(50%);
            display: inline-flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 40px;
        }
        &:hover {
            box-shadow: 0 0 11px rgba(33, 33, 33, 0.2);
            background: rgba(243, 146, 0, 0.04);
            .icon-category {
                background: $main-color;
            }
            svg {
                path[fill="#3a7bbf"],
                circle[fill="#f0f5f9"],
                rect[fill="#f39200"] {
                    fill: #fff;
                }
                path[fill="#ffc943"],
                path[fill="#3a7bbf"],
                path[fill="#f28623"],
                path[fill="#f39200"] {
                    fill: #fff;
                }
                path[fill="#f39200"],
                path[fill="#ffa245"],
                circle[fill="#f0f5f9"],
                path[fill="#f6931e"] {
                    fill: #fff;
                }
                path[stroke="#f39200"] {
                    stroke: #fff;
                }
                path[stroke="#3a7bbf"] {
                    stroke: #fff;
                }
            }
        }
    }
}
